<template>
    <div>
        <v-template-module :propTitle='title' :propContent='content'></v-template-module>
    </div>
</template>
<script>
    import VTemplateModule from './ad.vue'
    export default{
        components:{
            VTemplateModule
        },
        data(){
            return {
                title:'数据统计',
                content:'历史资料、科学实验、检验、统计等所获得的和用于科学研究、技术设计、查证、决策等的数值加以统计为解决方案做前期准备。'
            }
        }
    }
</script>


