<template>
    <div class='dialog' v-if='isShow'>
        <div class="mask" @click='closeMe'></div>
        <div class="dialog-content">
            <a href="javascript:;" @click='closeMe' class="close">X</a>
            <slot></slot>
        </div>       
    </div>
</template>   
<script>
    export default {
        props:{
            isShow:{
                type:Boolean
            }
        },
        methods:{
            closeMe(){
                this.$emit('on-close')
            }
        }
    } 
</script>
<style scoped lang="scss">
    $dialogWidth:60%;
    $dialogPadding:25px;
    @-webkit-keyframes fadeIn{
        0%{opacity:0}
        100%{opacity:1}
    }
    @-moz-keyframes fadeIn{
        0%{opacity:0}
        100%{opacity:1}
    }
    .dialog-content{
    -webkit-animation:fadeIn .6s .2s ease both;
    -moz-animation:fadeIn .6s .2s ease both;
    }
    
    .mask{
        position:absolute;
        background-color:rgba(2,2,2,0.5);
        top:0;
        bottom:0;
        right: 0;
        left: 0;
        height:150vh;
    }
    .dialog-content{
        position:fixed;
        z-index: 100;
        background-color:#fff;
        box-shadow:0px 0px 24px #222;
        top:50%;
        left: 50%;
        transform: translate(-50%, -50%); 
        padding: $dialogPadding;
        width:$dialogWidth;
        .close{
            position:absolute;
            right:20px;
        }
    }
</style>


