<template>
    <div @mouseover='clearTimer' @mouseout='setTimer' class="carousel">
        <transition-group  name='carousel' mode='in-out' tag='ul' class='img-wrap'>
            <li v-for='(item,index) in slides' 
                v-show='index===curIndex'
                :key='index' 
            >
                <a href="">
                    <img :src="item.url" :alt="item.alt">
                </a>
            </li>
        </transition-group>
        <div class="tab clearfix">
            <div v-for='(item,index) in slides' 
              v-text='item.alt'
              v-show='index===curIndex'
              class="title"
            >
            </div>
            <div class="tabLis clearfix">
                <a @click='goTo(preIndex)' href="javascript:;">&lt;</a>
                <div class="order">
                    <a v-for='(item,index) in slides'
                     href="javascript:;"
                     v-text='index+1'
                     @click.prevent='goTo(index)'
                     :class='{active:index===curIndex}'
                    ></a>
                </div>
                <a @click='goTo(nextIndex)' href="javascript:;">&gt;</a>
            </div>
        </div>
    </div>
</template>
<script>
    export default {
        name:'slideShow',
        props:['slides'],
        data(){
            return {
                curIndex:0,
                timer:null,
                interval:1200
            }
        },
        computed:{
            preIndex(){
                if(this.curIndex===0){
                    return this.slides.length-1
                }else{
                    return this.curIndex-1;
                }
            },
            nextIndex(){
                if(this.curIndex===this.slides.length-1){
                    return 0
                }else{
                    return this.curIndex+1;
                }
            }
        },
        methods:{
            goTo(index){
                this.curIndex=index;
            },
            setTimer(){
                this.timer=setInterval(()=>{
                    this.goTo(this.nextIndex)
                },this.interval)
            },
            clearTimer(){
                clearInterval(this.timer);
            }
        },
        mounted(){
            this.setTimer();
        }
    }
</script>
<style lang="scss" scoped>
    $height : 500px;
    $width:900px;
    $tabHeight:40px;
    .carousel{
        width:$width;
        height:$height;
        position:relative;
        overflow:hidden;
        .img-wrap{
            width:$width*4;
            position:relative;
            a{
                display:block;
            }
            li{
                position:absolute;
            }
            img{
                width:$width;
                height:$height;
            }
        }
        .tab{
            width:100%;
            height:$tabHeight;
            background-color:rgba(2,2,2,0.4);
            position:absolute;
            box-sizing:border-box;
            padding:0 40px 0 40px;
            bottom:0px;
            .title{
                float:left;
                color:#fff;
                line-height:$tabHeight;
            }
            .tabLis{
                float:right;
                 a{
                    color:#fff;
                    line-height:$tabHeight;
                    margin:0 6px;
                    &:hover{
                        text-decoration: underline;
                    }
                }
                >*{
                    float:left;
                }
                .order{
                    .active{
                        background-color:#af1829;
                    }
                }
            }
        }
    }
    .carousel-enter-active {
        transition: all 0.4s ease;
        transform: translateX(0);
    }
    .carousel-leave-active {
        transform: translateX(-100%);
        transition: all 0.4s ease;
    }
    .carousel-enter {
        transform: translateX(100%);
        opacity:1;
    }
    .carousel-leave {
        transform: translateX(0);
    }
    
</style>


