<template>
  <div id='app'>
    <div id="header">
      <div class="header-center">
        <router-link class="head-portrait" :to="{path: '/market'}">
          <img src="http://oph3rwqhn.bkt.clouddn.com/17-6-6/96691532.jpg">
        </router-link>
        <ul class="tab">
          <li v-if='!userName'><a class="lll" href="/" @click.prevent='isShowLoginDialog=true'>登录</a></li>
          <li v-else><a href='javascript:;'>{{userName}}</a></li>
          <li v-if='!userName'><a href="/about" @click.prevent='isShowRegDialog=true'>注册</a></li>
          <li v-else><a href='javascript:;'>退出</a></li>
          <li><a href="/list" @click.prevent='isShowAboutDialog=true'>关于</a></li>
        </ul>
      </div>
    </div>
    <div id="main">
      <router-view></router-view>
    </div>
    <div id="footer">
      <div class="link">
        <a href="" class="iconfont icon-matuwang-zhihufangxingdianji"></a>
        <a href="" class="iconfont icon-github"></a>
        <a href="" class="iconfont icon-jian"></a>
      </div>
    </div>
    <the-dialog @on-close="closeDialog('isShowAboutDialog')" :is-show='isShowAboutDialog'>
      <p>about</p>
    </the-dialog>
    <the-dialog @on-close="closeDialog('isShowLoginDialog')" :is-show='isShowLoginDialog'>
      <login-dialog @on-login='afterLogin'></login-dialog>
    </the-dialog>
    <the-dialog @on-close="closeDialog('isShowRegDialog')" :is-show='isShowRegDialog'>
      <p>sign up</p>
    </the-dialog>
  </div>
</template>
<script>
  import Dialog from './components/dialog'
  import LoginDialog from './components/login'
  export default {
    data(){
      return {
        isShowLoginDialog:false,
        isShowAboutDialog:false,
        isShowRegDialog:false,
        userName:''
      }
    },
    methods:{
      closeDialog(arg){
        this[arg]=false;
      },
      afterLogin(msg){
        this.isShowLoginDialog=false;
        this.userName=msg
      }
    },
    components:{
      theDialog:Dialog,
      loginDialog:LoginDialog
    },
    name: 'layout'
  }

</script>
<style lang="scss">
  /*reset*/
  ul,li,dl,dt,dd{
      padding: 0;
      margin: 0;
      list-style: none;
  }
  h1,h3,h2{
      padding: 0;
      margin: 0;
  }
  body{
      background-color: #f1f2f5;
      font-size:14px;
  }
  .clearfix:after{
    content:'';
    display:block;
    clear:both;
  }

  #header {
    height: 100px;
  }

  #footer {
    height: 100px;
  }

  $large:1104px;
  $middle:800px;
  a {
    text-decoration: none;
  }
  #header {
    height: 80px;
    background: url('http://oph3rwqhn.bkt.clouddn.com/17-6-6/55143318.jpg') 0px 0 no-repeat;
    background-size: cover;
    color: #fff;
    .header-center {
      display: flex;
      justify-content: space-between;
      height: 100%;
      width: 1200px;
      margin: 0 auto;
      text-align: center;
      .head-portrait {
        display: flex;
        align-items: center;
        >img {
          width: 68px;
          height: 68px;
        }
      }
      .tab {
        display: flex;
        height: 100%;
        width: 20%;
        justify-content: flex-end;
        align-items: stretch;
        line-height: 80px;
        >li {
          flex-grow: 1;
        }
        li>a {
          color: #fff;
          font-size: 18px;
          &:hover {
            transition: 0.4s;
            text-decoration: none;
            color: #f9bf3b;
          }
        }
      }
    }
    @media (max-width:$large) {
      .header-center {
        width: 100%;
      }
    }
  }

  #main{
      width: 1200px;
      margin: 20px auto ;
  }

  #footer {
    height: 50px;
    background: url('http://oph3rwqhn.bkt.clouddn.com/17-6-6/55143318.jpg') center no-repeat;
    background-size: cover;
    .link {
      width: 1200px;
      height: 100%;
      margin: 0 auto;
      display: flex;
      justify-content: center;
      align-items: center;
      a {
        display: block;
        color: #fff;
        font-size: 30px;
        margin-left: 40px;
        &:nth-child(2) {
          position: relative;
          top: -3px;
        }
        &:hover {
          text-decoration: none;
          transition: 0.4s;
          color: #f9bf3b;
        }
      }
    }
  }

</style>



