// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import Layout from './layout'
import 'normalize.css'
Vue.config.productionTip = false
import VueRouter from 'vue-router'
import IndexPage from './pages/index'
import DetailPage from './pages/detail'
import DetailAd from './pages/detail/ad'
import DetailForecast from './pages/detail/forecast'
import DetailCount from './pages/detail/count'
import DetailAnalyse from './pages/detail/analyse'
import OrderPage from './pages/order.vue'
/* eslint-disable no-new */

import ElementUI from 'element-ui'  
import 'element-ui/lib/theme-default/index.css'    
Vue.use(ElementUI)  

Vue.use(VueRouter);
let router = new VueRouter({
  mode:'history',
  routes:[
    {
      path:'/market',
      component:IndexPage,
    },
    {
      path:'/market/order',
      component:OrderPage
    },
    {
      path:'/market/detail',
      component:DetailPage,
      redirect:'/analyse',
      children:[
        {
          path:'forecast',
          component:DetailForecast
        },
        {
          path:'count',
          component: DetailCount
        },
        {
          path:'analyse',
          component:DetailAnalyse
        },
        {
          path:'ad',
          component:DetailAd
        }
      ]
    }
  ]
})

new Vue({
  el: '#app',
  router,
  template: '<Layout/>',
  components: { Layout,OrderPage }
})


