<template>
    <div>
        <v-template-module :propTitle='title' :propContent='content'></v-template-module>
    </div>
</template>
<script>
    import VTemplateModule from './ad.vue'
    export default{
        components:{
            VTemplateModule
        },
        data(){
            return {
                title:'数据分析',
                content:'是指在获得网站访问量基本数据的情况下对有关数据进行统计、分析，从中发现用户访问网站的规律，并将这些规律与网络营销策略等相结合，从而发现目前网络营销活动中可能存在的问题，并为进一步修正或重新制定网络营销策略提供依据。当然这样的定义是站在网络营销管理的角度来考虑的'
            }
        }
    }
</script>


