<template>
    <div class='ad-wrap'>
        <h3 v-text='propTitle' class='ad-title'>广告发布</h3>
        <p v-text='propContent' class='descript'>
            广告活动按照广告计划执行，到完成广告创作并形成广告作品之后，经过广告主的最后审核同意，即可送到预定的媒介发布刊播。这项工作一般由媒介部门的有关专业人员负责，他们的任务就是专门负责与有关媒介单位接洽，安排有关广告的发播事宜，并对发播质量实施监督。
        </p>
        <div class="form">
            <div class="count-wrap">
                <div class='label'>购买数量：</div>
                <v-count @on-change='saveCount' :max="50" :min="10"></v-count>
            </div>
            <div class="selection-wrap">
                <div class='label'>行业：</div>
                <v-selection @on-change='saveSelection' :selections='selection'></v-selection>
            </div>
            <div class="checkbox-wrap">
                <div class='label'>产品版本：</div>
                <v-checkbox @on-change=saveVersion :selections='arrCheckbox'></v-checkbox>
            </div>
            <div class="effectTime">
                <div class='label'>时间：</div>
                <span>半年</span>
            </div>
            <div class="price">
                <div class='label'>总价：</div>
                <span>{{order.count*unitPrice}}</span>
            </div>
            <div>
                <a @click='submit' class='btn' href='javascript:;'>提交订单</a>
            </div>
        </div>
        <v-dialog @on-close='close' :is-show='isSubmit'>
            <div class="order-info">
                <h3>订单信息：</h3>
                <p>数量：{{order.count}}</p>
                <p>行业：{{order.trade.label}}</p>
                <p>产品版本：<span v-for='item in order.version'>{{item.label + ' '}}</span></p>
                <p>时间：半年</p>
                <p>总价：{{order.count*unitPrice}}</p>
            </div>
            <div class="hr"></div>
            <h3>请选择银行：</h3>
            <v-bank @on-change='getBank'></v-bank>
            <div>
                <router-link :to="{path: '/market/order'}" @click='comfirmBank' class='btn' href='javascript:;'>确定付款</router-link>
            </div>
        </v-dialog>
    </div>
</template>
<script>
    import VCount from '../../components/count'
    import VSelection from '../../components/selection.vue'
    import VCheckbox from '../../components/checkbox.vue'
    import VDialog from '../../components/dialog.vue'
    import VBank from '../../components/bank-chose.vue'
    let selection=[{
        label:'出版业',
        value:0
    },{
        label:'金融业',
        value:1
    },{
        label:'互联网',
        value:2
    },{
        label:'新媒体',
        value:3
    }];
    let arrCheckbox=[
        {
          label: '初级版',
          value: 0
        },
        {
          label: '中级版',
          value: 1
        },
        {
          label: '高级版',
          value: 2
        },
        {
          label: '专家版',
          value: 3
        }
      ];
    export default{
        components:{
            VCount,
            VSelection,
            VCheckbox,
            VDialog,
            VBank
        },
        props:{
            propTitle:{
                type:String,
                default:'广告发布'
            },
            propContent:{
                type:String,
                default:'广告活动按照广告计划执行，到完成广告创作并形成广告作品之后，经过广告主的最后审核同意，即可送到预定的媒介发布刊播。这项工作一般由媒介部门的有关专业人员负责，他们的任务就是专门负责与有关媒介单位接洽，安排有关广告的发播事宜，并对发播质量实施监督。'
            }
        },
        data(){
            return {
                order:{
                    count:10,
                    trade:selection[0],
                    version:[arrCheckbox[0]],
                    bankChose: {
                        id: 201,
                        label: '招商银行',
                        name: 'zhaoshang'
                    }
                },
                selection:selection,
                arrCheckbox:arrCheckbox,
                unitPrice:100,
                isSubmit:false,
                checkOrderControl:false
            }
        },
        methods:{
            saveCount(arg){
                this.order.count=arg
            },
            saveSelection(arg){
                this.order.trade=arg
            },
            saveVersion(arg){
                this.order.version=arg
            },
            submit(){
                for(let attr in this.order){
                    if(this.order[attr].length===0){
                        this.isSubmit=false;
                        return
                    }
                }
                this.isSubmit=true;
            },
            close(){
                this.isSubmit=false;
            },
            comfirmBank(){
                console.log(this.order)
                this.close();
            },
            getBank(val){
                this.order.bankChose='val'
            }
        }
    }   
</script>
<style lang="scss">
    .ad-wrap{
        padding: 0 10px;
    }
    .ad-title{
        padding: 20px 0;
    }
    .descript{
        color:#a499b2;
        text-indent:2em;
        line-height:1.6;
    }
    .form{
        padding: 20px 0;
        >*{
            padding: 6px 18px;
        }
    }
    .label{
        display: inline-block;
        width: 90px;
    }
    .hr{
        height: 0px;
        border-bottom: 1px solid #aaa;
        margin: 10px 0; 
    }
</style>


