<template>
    <div>
        <h3 class='order-title'>您的产品：</h3>
        <div class="order-nav">
            <label>选择产品：</label>
            <v-selection @on-change='getSelection' :selections='selection'></v-selection>
            <label>开始时间：</label>
            <el-date-picker
            v-model="startTime"
            type="datetime"
            size='small'
            placeholder="选择日期时间">
            </el-date-picker>
            <label>结束时间：</label>
            <el-date-picker
            v-model="endTime"
            type="datetime"
            size='small'
            placeholder="选择日期时间">
            </el-date-picker>
        </div>
        <div class='order-list'>
            <el-table
            :data="orderLis"
            style="width: 100%">
                <el-table-column
                    prop="orderId"
                    label="订单号">
                </el-table-column>
                <el-table-column
                    prop="product"
                    label="购买产品">
                </el-table-column>
                <el-table-column
                    prop="type"
                    label="版本类型">
                </el-table-column>
                <el-table-column
                    prop="time"
                    label="有效时间">
                </el-table-column>
                <el-table-column
                    prop="date"
                    label="购买时间">
                </el-table-column>
                <el-table-column
                    prop="count"
                    label="购买数量">
                </el-table-column>
                <el-table-column
                    prop="totalPrice"
                    label="价格">
                </el-table-column>
            </el-table>
        </div>
    </div>
</template>
<script>
    let selection=[{
        label:'出版业',
        value:0
    },{
        label:'金融业',
        value:1
    },{
        label:'互联网',
        value:2
    },{
        label:'新媒体',
        value:3
    }];
    let data = [
        {
            orderId:'ddj123',
            product : '数据统计',
            type : '高级版	',
            time : '1年',
            date : '2016-10-10',
            count : '2',
            totalPrice : '500元'
        },
        {
            orderId:'yuj583',
            product : '流量分析',
            type : '户外版',
            time : '3个月',
            date : '2016-5-2',
            count : '1',
            totalPrice : '2200元'
        },
        {
            orderId:'pmd201',
            product : '广告发布',
            type : '商铺版',
            time : '3个月',
            date : '2016-8-3',
            count : '12',
            totalPrice : '7890元'
        }
    ]
    import VSelection from '../components/selection.vue'
    import {DateTimePicker,Table} from 'element-ui'
    export default{
        components:{
            VSelection
        },
        data(){
            return {
                selection:selection,
                selected:selection[0],
                startTime:'',
                endTime:'',
                orderLis : data
            }
        },
        methods:{
            getSelection(val){
                this.selected=val
            }
        }
    }
    
</script>
<style scoped lang="scss">
    .order-title {
        font-size: 20px;
    }
    .order-nav{
        padding: 18px 0;
    }
    .order-nav>label{
        display: inline-block;
        width: 120px;
        text-align: center;
    }
</style>


