<template>
  <div class="index-wrap">
    <div class="main-body">
      <slide-show :slides='slides'></slide-show>
      <div class="sell-product">
        <ul class='sell-list'>
          <li v-for='(item,index) in sellProduct' :class='{"fix-margin":index%2===0}'>
            <img :src="item.img">
            <div class="sell-content-wrap">
              <h3>{{item.title}}</h3>
              <p>{{item.content}}</p>
              <a class='btn' :href="item.href">立刻购买</a>
            </div>
          </li>
        </ul>
      </div>
    </div>
    <div class="main-side">
      <div class="main-product">
        <h3>全部产品</h3>
        <dl v-for='item in sideData'>
          <dt>{{item.title}}</dt>
          <dd :class='{hot:index===item.hot}' v-for='(childItem,index) in item.list'>
            <a target="_blank" :href="childItem.url">{{childItem.name}}</a>
          </dd>
        </dl>
      </div>
      <div class="main-news">
        <h3>最新消息</h3>
        <ul>
          <li v-for='item in sideNews'><a target="_blank" href="http://www.jianshu.com/u/5404fe848e31">{{item}}</a></li>
        </ul>
      </div>
    </div>
  </div>
</template>
<script>
  import {fetch} from '../js/fetch';
  import slideShow from '../components/slideShow'
  export default {
    name: 'index',
    components:{
      slideShow
    },
    data() {
      return {
        sideData: null,
        sideNews: null,
        sellProduct:null,
        slides:[
            {
                "url": "http://oph3rwqhn.bkt.clouddn.com/17-5-5/59901775-file_1493977113783_12ccb.jpg",
                "alt": "\u5411\u7740\u5149"
            },
            {
                "url": "http://oph3rwqhn.bkt.clouddn.com/17-5-5/39871478-file_1493977112193_16ba2.jpg",
                "alt": "\u5496\u5561"
            },
            {
                "url": "http://oph3rwqhn.bkt.clouddn.com/17-5-5/20785557-file_1493977111062_822c.jpg",
                "alt": "\u5199\u4ee3\u7801"
            },
            {
                "url": "http://oph3rwqhn.bkt.clouddn.com/17-5-5/95829360-file_1493977206229_eb1e.jpg",
                "alt": "\u4e16\u754c\u4f60\u597d"
            }
        ]
      }
    },
    beforeCreate() {
      //产品列表
      fetch('http://rapapi.org/mockjsdata/18455/getSideMsg').then(val => {
        this.sideData = val.data;
      })
      //新闻获取
      fetch('http://rapapi.org/mockjsdata/18455/getSideNews').then(val => {
        this.sideNews = val.data.news;
      })
      //售卖列表
      fetch('http://rapapi.org/mockjsdata/18455/getSellProduct').then(val=>{
        this.sellProduct=val.data.productLis
      })
      //轮播图片
      // fetch('http://rap.taobao.org/mockjsdata/18455/getCarousel').then(val=>{
      //   this.slides=val.data.lis
      // })
    },
    methods: {

    }
  }

</script>
<style scoped lang="scss">
  @mixin bor {
    border: 1px solid #aaa;
  }
  .clearfix:after{
    content: '';
    display:block;
    clear: both;
  }
  a {
    color: #222;
    &:hover {
      text-decoration: underline;
    }
  }
  .hot{
    position: relative;
    &:after{
     content: 'HOT';
     display: inline;
     position:absolute;
     left:88px;
     background-color:#ff0000;
     color:#fff;
    }
  }
  .main-side {
    width: 260px;
    >* {
      background-color: #fff;
    }
    h3 {
      font-size: 14px;
      padding: 5px 0 5px 12px;
      background-color: #4fc08d;
      color: #f1f2f5;
    }
    .main-product {
      dt {
        font-weight: 600;
        padding: 10px 0 10px 12px;
      }
      dd {
        padding: 4px 0 4px 24px
      }
      dl {
        padding: 8px 0 12px 6px;
      }
      & dl:not(:last-child) {
        border-bottom: 1px solid #aaa;
      }
    }
    .main-news {
      margin-top: 24px;
      min-height: 520px;
      ul {
        padding: 6px 0 0 6px;
      }
      li {
        padding: 6px 4px 6px 12px;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
      }
    }
  }
  .main-body{
    float: right;
    @extend .clearfix;
    .sell-product{
      width: 900px;
      .sell-list{  
        @extend .clearfix;
        li{
          float:left;
          padding:20px;
          background-color:#fff;
          margin-right:0;
          margin-top:20px;
          &.fix-margin{
            margin-right:20px;
          }
          img{
            float:left;
          }
          .sell-content-wrap{
            width:280px;
            margin-left:120px;
            >*{
              margin-top:14px;
            }
          }
          
        }
      }
    }
  }
  .btn{
      background: #4fc08d;
      color: #fff;
      display: inline-block;
      padding: 10px 20px;
      cursor: pointer;
    }
</style>



