import VueResource from 'vue-resource'
import Vue from 'vue'
Vue.use(VueResource)

function fetch(url, data = null) {
  return new Promise((resolve, reject) => {
    Vue.http({
      url,
      method: 'post',
      params: data
    }).then(function(response) {
        let result = response.data
        if (typeof(result) === "string") {
          result = JSON.parse(result.data)
        }
        if (result.status === 1) {
          resolve(result)
        } else {
          reject(result)
        }
      },
      function(response) {
        reject({
          status: -1,
          message: '系统错误，请稍后重试'
        })
      })
  })
}

export {
    fetch
}


