<template>
    <div>
        <v-template-module :propTitle='title' :propContent='content'></v-template-module>
    </div>
</template>
<script>
    import VTemplateModule from './ad.vue'
    export default{
        components:{
            VTemplateModule
        },
        data(){
            return {
                title:'数据预测',
                content:'未来，大数据会变得越来越重要，其核心应用预测也会成为互联网行业以及产业变革的重要力量，我们很有必要对数据预测及其分析方法进行全面且深入的了解。在这一点上，《大数据预测》是本很好的读物，适合大数据所有相关行业的人阅读。'
            }
        }
    }
</script>


