<template>
    <div class='login-wrap'>
        <div class='username'>
            <label>用户名：</label>
            <input placeholder="输入11位手机号" 
                v-model='user' 
                type='text'
                @blur='isValidateName'
            />
            <p class='warning'>{{warning.user}}</p>
        </div>
        <div class='password'>
            <label>密&nbsp;&nbsp;&nbsp;码：</label>
            <input @blur='isValidatePwd' placeholder="随便输入什么" v-model="pwd" type='password'>
            <p class='warning'>{{warning.pwd}}</p>
        </div>
        <a @click='toLogin' @keyup.enter='toLogin' href="javascrpt:;" class="login-btn">登&nbsp;&nbsp;录</a>
    </div>
</template>
<script>
    import {checkphone} from '../js/validate'
    export default{
        data(){
            return{
                user:'',
                pwd:'',
                warning:{
                    user:' ',
                    pwd:' '
                }
            }
        },
        methods:{
            isValidateName(){
                if(this.checkName){
                    this.warning.user=' '
                    return true;
                }else{
                    this.warning.user='请输有效手机号'
                    return false;
                }
            },
            isValidatePwd(){
                if(this.checkPwd){
                    this.warning.pwd=' '
                    return true;
                }else{
                    this.warning.pwd='随便输入什么'
                    return false;
                }
            },
            toLogin(){
                if(this.isValidateName()&&this.isValidatePwd()){
                    this.$emit('on-login',this.user)
                    console.log(13)
                }
            }
        },
        computed:{
            checkName(){
                if(this.user){
                    return checkphone(this.user)
                }else{
                    return false;
                }
            },
            checkPwd(){
                return this.pwd? true : false;
            },
        }
    }
</script>
<style scoped lang="scss">
    $fontSize:16px;
    $lineHeight:1.5em;
    .login-wrap{
        font-size: $fontSize;
        line-height: $lineHeight;
        text-align: center;
        padding: 4px;
        >div{
            margin-bottom: 20px;
        }
        .warning{
            font-size:12px;
            color:red;
            line-height: 1em;
        }
    }
    .login-btn{
        display: inline-block;
        padding: 5px;
        width: 80px;
        height:30px;
        line-height: 30px;
        background-color:#4fc08d;
        color:#fff;
    }
</style>


