<template>
    <div class='clearfix'>
        <div class="detail-content">
            <keep-alive>
                <router-view></router-view>
            </keep-alive>
        </div>
        <div class="detail-nav">
            <dl>
                <dt>
                    <img :src="routerSrc" alt="">
                </dt>
                <router-link active-class='active' :key='index' v-for='(item,index) in product' tag='dd' :to="{path:item.path}">{{item.name}}</router-link> 
            </dl>
        </div>
    </div>
</template>
<script>
    let imgSrc={
        '/jirengu-inc/jrg-renwu8/homework/林伟琨/market/detail/analyse':'http://i2.muimg.com/519918/a12eda18e98e6a49.png',
        '/jirengu-inc/jrg-renwu8/homework/林伟琨/market/detail/forecast':'http://i2.muimg.com/519918/9d7dce04a3540fe7.png',
        '/jirengu-inc/jrg-renwu8/homework/林伟琨/market/detail/count':'http://i2.muimg.com/519918/dff5d270d5e9676e.png',
        '/jirengu-inc/jrg-renwu8/homework/林伟琨/market/detail/ad':'http://i2.muimg.com/519918/364c3f18a75e7b6b.png'
    }
    let product = [
                    {
                        name: '数据统计',
                        path: 'count'
                    },
                    {
                        name: '数据预测',
                        path: 'forecast'
                    },
                    {
                        name: '流量分析',
                        path: 'analyse'
                    },
                    {
                        name: '广告发布',
                        path: 'ad'
                    }
                ]
    export default{
        data(){
            return {
                imgSrc:imgSrc,
                product:product
            }
        },
        mounted(){
            console.log(this.imgSrc[this.$route.path])
        },
        computed:{
            routerSrc(){
                 return this.imgSrc[this.$route.path]
            }
        }
    }
</script>
<style lang='scss'>
    $navWidth:200px;
    dd.active{
        background-color:#4fc08d;
        color:#fff;
    }
    .detail-nav{
        float:left; 
        width: $navWidth;
        background-color: #fff;
        text-align: center;
        a{
            color:#222;
        }
        dd{
            padding: 8px 0px;
            cursor: pointer;
            &:hover{
                @extend dd.active;
            }
        }
    }
    .detail-content{
        float: right;
        background-color: #fff;
        width: 1200px-30px-$navWidth;
    }
    .btn{
        padding: 8px 14px;
        color:#fff;
        display: inline-block;
        background-color:#4fc08d;
        margin-top: 10px;
        &:hover{
            text-decoration: underline;
        }
    }
</style>


